  border-top: 2px solid #fff;
}

.fieldset-container .fieldset:first-child {
  border-top: 0;
}

.fieldset-container-header {
  position: relative;
  padding: 10px 24px 11px 24px;
  transition: color 0.15s cubic-bezier(0.2, 0.3, 0.25, 0.9), border 0.15s cubic-bezier(0.2, 0.3, 0.25, 0.9), background 0.15s cubic-bezier(0.2, 0.3, 0.25, 0.9);
  border-radius: 3px;
  cursor: pointer;
}

.fieldset-container-header:before {
  position: absolute;
  left: 0px;
  top: 0;
  display: inline-block;
  content: " ";
  width: 0;
  height: 48px;
  transition: border 0.15s cubic-bezier(0.2, 0.3, 0.25, 0.9);
}

.fieldset-container-header h3:before {
  content: "keyboard_arrow_right";
  margin-left: -59px;
  margin-right: 34px;
  font-size: 1.5rem;
  transition: transform 0.15s cubic-bezier(0.2, 0.3, 0.25, 0.9);
}

