}

.fieldset-container-header[aria-expanded=false]:before {
  border-right: 2px solid #e6f6f7;
}

.fieldset-container-header[aria-expanded=false] h3:before {
  color: #5f6161;
  transform: rotate(0);
}

.fieldset-container-header[aria-expanded=false]:hover {
  color: #5f6161;
  border-left-color: #e0e0e1;
  background: #e0e0e1;
}

.fieldset-container-header[aria-expanded=false]:hover:before {
  border-right: 2px solid #f2f2f2;
}

.fieldset-container-header[aria-expanded=false]:hover h3:before {
  color: #5f6161;
  transform: rotate(90deg);
}

.fieldset-container-header[aria-expanded=true] {
  color: #505152;
  border-bottom-right-radius: 0;
  border-left: 48px solid #d8d8da;
  background: #d8d8da;
}

.fieldset-container-header[aria-expanded=true]:before {
  border-right: 2px solid #f2f2f2;
}

.fieldset-container-header[aria-expanded=true] h3:before {
  color: #505152;
  transform: rotate(90deg);
}

.fieldset {
  display: block;
  padding: 12px 24px 12px 24px;
  border-radius: 3px;
  background: #f2f2f2;
}

